<template>
  <div style="position:relative">
    <div class="topTitle">
      <span class="topTitle-span1">审核任务</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">业务洗钱风险审核任务</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">调整待审核</span>
      <div class="button-content">
        <!-- <Button type="primary" @click="backHome">返回</Button> -->
      </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBody">
        <!-- <div class="taskAuditDataExtraction-left"> -->
        <div>
          <div class="contentCellTitle">
            <div class="contentCellTitle-1"></div>确认信息
          </div>
          <Row>
            <table class="tableBox" style="margin-top:0">
              <thead>
                <tr>
                  <td v-for="theads in tableHeader">{{theads.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td v-for="tbodys in tableBody" :title="tbodys.assistFlag?tbodys.val:''" :class="{'spanBox textMaxWidth textOverflow':tbodys.assistFlag}" @click="showDialog(tbodys)">{{tbodys.val}}</td>
                </tr>
              </tbody>
            </table>
          </Row>
          <div class="contentCellTitle">
            <div class="contentCellTitle-1"></div>流程信息
          </div>
          <Row>
            <table class="tableBox" style="margin-top:0">
              <thead>
                <tr>
                  <td>流程名称</td>
                  <td colspan="8">{{processTable.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>流程步骤</td>
                  <td>发起时间</td>
                  <td>结束时间</td>
                  <td>办理人</td>
                  <td>办理人部门</td>
                  <td>结果</td>
                  <td>意见</td>
                  <td>下一步骤</td>
                  <td>下一处理人</td>
                </tr>
                <tr v-for="(item ,index) in processTable.content" :key="index">
                  <td>{{item.actionName}}</td>
                  <td>{{item.startTime}}</td>
                  <td>{{item.endTime}}</td>
                  <td>{{item.userName}}</td>
                  <td>{{item.userDeptName}}</td>
                  <td>{{item.approveResult==0||item.approveResult=='null'?'审核通过':'审核退回'}}</td>
                  <td
                    class="opinions"
                    :title="item.approveNote=='null'?'通过':item.approveNote"
                  >{{item.approveNote=='null'?'通过':item.approveNote}}</td>
                  <td>{{item.nextActivityName}}</td>
                  <td>{{item.nextUserName}}</td>
                </tr>
              </tbody>
            </table>
          </Row>
          <div class="contentCellTitle">
            <div class="contentCellTitle-1"></div>上传附件
          </div>
          <Row>
            <Upload
              ref="upload"
              multiple
              type="drag"
              :show-upload-list="false"
              :data="uploadParams"
              :default-file-list="defaultList"
              :format="['zip','rar','xls','xlsx','doc','docx','pdf']"
              :max-size="10240"
              :on-format-error="handleFormatError"
              :on-exceeded-size="handleMaxSize"
              :before-upload="handleUploadfile"
              :action="axios.defaults.baseURL+'/bpm/upload'"
              :on-success="handleSuccess"
              :on-error="handleError"
            >
              <div style="padding: 20px 0">
                <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
                <p>请上传附件</p>
              </div>
            </Upload>
            <div>上传格式为zip、rar、xls、xlsx、doc、docx、pdf，且大小不超过10MB</div>
          </Row>
          <Table border :columns="columns1" :data="uploadList"></Table>
          <!-- ============= -->
        </div>
        <!-- <div class="taskAuditDataExtraction-right"> -->
        <div>
          <Row class="tabBox">
            <div class="taskAuditDataExtraction-right-cell">
              <div class="taskAuditDataExtraction-right-cell1">提示：</div>
              <div class="taskAuditDataExtraction-right-cell2 flexStart">
                <div class="taskAuditDataExtraction-right-cell2-cell">
                  <span>当前处于：</span>
                  <span>{{nextActivityName}}</span>
                </div>
                <div class="taskAuditDataExtraction-right-cell2-cell">
                  <span>当前处理人：</span>
                  <span>{{nextUserName}}</span>
                </div>
              </div>
            </div>

            <Row style="padding:0 5px">
              <Tabs value="name1" v-model="tabname" @on-click="swithTab" :animated="false">
                <TabPane label="审核" name="name1">
                  <Form
                    ref="formValidate"
                    :model="formValidate"
                    :rules="ruleValidate"
                    :label-width="5"
                  >
                    <div class="taskAuditDataExtraction-right-cell1">意见：</div>
                    <FormItem label prop="desc">
                      <Input
                        v-model="formValidate.desc"
                        type="textarea"
                        :autosize="{minRows: 2,maxRows: 5}"
                        placeholder="请输入审核意见"
                      ></Input>
                    </FormItem>
                    <div class="taskAuditDataExtraction-right-cell1">下一步操作：</div>
                    <FormItem label prop="gender">
                      <RadioGroup v-model="formValidate.gender">
                        <Radio label="0">审核通过</Radio>
                        <Radio label="1">审核退回</Radio>
                      </RadioGroup>
                    </FormItem>
                    <FormItem label prop="treeArr1" v-show="showTree1Flg && lastStepFlag">
                      <Tree
                        ref="treeRef"
                        :data="treeArr1"
                        show-checkbox
                        multiple
                        @on-check-change="updateNextUserInfo"
                      ></Tree>
                    </FormItem>
                    <Row class="flexCenter">
                      <Button
                        type="primary"
                        shape="circle"
                        style="width:100px;margin:20px"
                        @click="taskBtnFun('formValidate')"
                      >提交</Button>
                    </Row>
                  </Form>
                </TabPane>
                <TabPane label="转办" name="name2">
                  <div class="taskAuditDataExtraction-right-cell1">意见：</div>
                  <Form
                    ref="formValidate2"
                    :model="formValidate2"
                    :rules="ruleValidate2"
                    :label-width="5"
                  >
                    <FormItem label prop="desc">
                      <Input
                        v-model="formValidate2.desc"
                        type="textarea"
                        :autosize="{minRows: 2,maxRows: 5}"
                        placeholder="请输入审核意见"
                      ></Input>
                    </FormItem>
                    <div class="taskAuditDataExtraction-right-cell1">转办人员：</div>
                    <FormItem label prop="treeArr1">
                      <Tree
                        ref="treeRef"
                        :data="treeArr2"
                        show-checkbox
                        multiple
                        @on-check-change="updateNextUserInfo2"
                      ></Tree>
                    </FormItem>
                    <Row class="flexCenter">
                      <Button
                        type="primary"
                        shape="circle"
                        style="width:100px;margin:20px"
                        @click="doTransSubmit('formValidate2')"
                      >提交</Button>
                    </Row>
                  </Form>
                </TabPane>
              </Tabs>
            </Row>
          </Row>
          <div style="width:100%;height:200px"></div>
        </div>
      </div>
    </div>
    <Modal :mask-closable="false"   v-model="showModal" title="任务详情" class="dialog" :styles="{width: '80%'}">
      <component :is="tabview" :key="batchId" :data-objj="dataObjj" :dataObj="dataObj" width="80%"></component>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import assessDetail from "@/components/moneyRiskAssess/common/assessDetail"; //评估

export default {
  components: {
    assessDetail
  },
  created() {
    this.doTaskView();
  },
  beforeUpdate() {
    this.uploadList = this.$refs.upload.fileList;
  },
  watch: {
    "formValidate.gender": function(newValue, oldValue) {
      if (this.formValidate.gender == "1") {
        this.showTree1Flg = false;
      } else {
        this.showTree1Flg = true;
      }
    }
  },
  data() {
    const validaterNextUserInfo = (rule, value, callback) => {
      if (
        this.lastStepFlag &&
        this.formValidate.gender != "1" &&
        this.formValidate.treeArr1 == ""
      ) {
        return callback(new Error("请至少选择一个审核人"));
      } else {
        callback();
      }
    };
    const validaterNextUserInfo2 = (rule, value, callback) => {
      if (this.formValidate2.treeArr1 == "") {
        return callback(new Error("请至少选择一个审核人"));
      } else {
        callback();
      }
    };
    return {
      tableHeader:[],
      tableBody:[],
      type: "", //菜单类型
      nextActivityName: "", //当前处于
      nextUserName: "", //当前处理人
      nodeId: "", //节点id
      showTree1Flg: true,
      tabname: "name1", //name1:审核 name2:转办
      batchId: "",
      uploadParams: {},
      showModal: false,
      tabview: "",
      dataObjj:{},
      dataObj: {},
      defaultList: [],
      filesArr: [],
      lastStepFlag: true, //是否最后一步 是为false 否为true（usertask中activityId==nextNodeId为最后一步）
      treeParam: {
        bpmName: "",
        nodeId: "",
        roleName: ""
      },
      uploadList: [],
      columns1: [
        {
          title: "文件名",
          key: "name"
        },
        {
          title: "上传人",
          key: "creatorName"
        },
        {
          title: "操作",
          key: "",
          width: 150,
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.fileDownload(params.index);
                    }
                  }
                },
                "下载"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    disabled:
                      parseInt(params.row.activityId) !=
                      parseInt(JSON.parse(this.uploadParams.params).activityId)
                        ? true
                        : false,
                    size: "small"
                  },
                  on: {
                    click: () => {
                      this.remove(params.index);
                    }
                  }
                },
                "删除"
              )
            ]);
          }
        }
      ],
      formValidate2: {
        //审核操作
        gender: "0",
        treeArr1: "",
        desc: "", //申办意见
        desc1: "" //转办意见
      },
      formValidate: {
        //审核操作
        gender: "0",
        treeArr1: "",
        desc: "", //申办意见
        desc1: "" //转办意见
      },
      treeArr2: [], //转办的树
      // 审核的树
      treeArr1: [],
      ruleValidate: {
        gender: [
          { required: true, message: "Please select gender", trigger: "change" }
        ],
        treeArr1: [
          {
            required: false,
            validator: validaterNextUserInfo
          }
        ],
        desc: [
          {
            type: "string",
            max: 512,
            message: "长度最大不能超过512",
            trigger: "blur"
          }
        ]
      },
      ruleValidate2: {
        gender: [
          { required: true, message: "Please select gender", trigger: "change" }
        ],
        treeArr1: [
          {
            required: false,
            validator: validaterNextUserInfo2
          }
        ],
        desc: [
          {
            type: "string",
            max: 512,
            message: "长度最大不能超过512",
            trigger: "blur"
          }
        ]
      },
      informationTable: {
        extraTime: "", //extraTime
        extraUnit: "", //extraUnit
        busiScope: "", //busiScope
        permissionTime: "", //permissionTime
        permissionTime1: "", //permissionTime1
        emergeLevel: "" //emergeLevel
      },
      processTable: {
        name: "业务洗钱风险审核流程",
        content: []
      }
    };
  },
  methods: {
    remove(index) {
      let that = this;
      let data = {
        deleteChild: false,
        documentId: JSON.parse(this.uploadParams.params).documentId,
        fileId: this.uploadList[index].url,
        user: JSON.parse(this.uploadParams.params).user
      };
      this.axios
        .post("/bpm/delete", JSON.stringify(data), {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if ((response.code = "200")) {
            this.uploadList.splice(index, 1);
            that.$Message.success("删除成功");
          }
        })
        .catch(error => {});
    },
    // 获取业务洗钱风险审核流程列表
    getFileList(data) {
      let multData = new FormData();
      multData.append("documentId", data.documentId);
      this.axios
        .post("/bpm/list", multData, {
          headers: { "Content-Type": "multipart/form-data" }
        })
        .then(response => {
          if (response.code == "200") {
            let files = response.data.dataVo.files;
            files.forEach(element => {
              this.defaultList.push({
                name: element.name,
                creatorName: element.creatorName,
                activityId: element.activityId,
                url: element.id
              });
            });
          }
        })
        .catch(error => {});
    },
    // 获取审核任务列表
    doTaskView() {
      let documentId = this.$route.query.documentId;
      let data = {
        documentId: documentId
      };
      this.getFileList(data);
      this.axios
        .post("/bpm/doTaskView", JSON.stringify(data), {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response && response.code == "200") {
            let tableData = JSON.parse(response.data.confirmData);
            this.tableHeader = tableData.tableHeader;
            this.tableBody = tableData.tableBody;
            // let data = response.data.confirmData;
            // this.informationTable = data;
            // this.informationTable.extraTime = JSON.parse(
            //   this.informationTable.extraTime
            // );
            // this.informationTable.permissionTime = JSON.parse(
            //   this.informationTable.permissionTime
            // );
            let flowData = response.data.flowData;
            this.processTable.content = flowData.records.comments;
            let data1 = this.processTable.content[this.processTable.content.length - 1];
            this.nextActivityName = data1.nextActivityName;
            this.nextUserName = data1.nextUserName;
            this.filesArr = response.data.files;
            this.treeParam = {
              bpmName: flowData.userTask.serviceCode,
              nodeId: flowData.userTask.activityId,
              documentId: flowData.document.id,
              taskId: flowData.userTask.id,
              currentUser: flowData.userTask.ownerId,
              roleName: ""
            };
            this.uploadParams = {
              params: {
                activityId: flowData.userTask.activityId,
                documentId: flowData.document.id,
                taskId: flowData.userTask.id,
                user: flowData.userTask.ownerId,
                activityName: flowData.userTask.name
              }
            };
            this.uploadParams.params = JSON.stringify(this.uploadParams.params);
            if (response.data.nextNodeId == flowData.userTask.activityId) {
              this.lastStepFlag = false;
            }
            let param = Object.assign({}, this.treeParam);
            param.nodeId = "0" + (parseInt(param.nodeId) + 1);
            this.getNextRole(param, "name1");
          } else {
            this.$Message.error(response.message);
          }
        })
        .catch(error => {});
    },
    handleFormatError(file) {
      this.$Notice.warning({
        title: "附件格式错误",
        desc: "请选择zip、rar、xls、xlsx、doc、docx、pdf."
      });
    },
    handleMaxSize(file) {
      this.$Notice.warning({
        title: "超出了附件大小限制",
        desc: " 不应超过10M"
      });
    },
    swithTab() {
      if (this.tabname == "name2") {
        this.treeArr2.length > 0
          ? ""
          : this.getNextRole(this.treeParam, "name2");
      }
    },
    //查询角色信息
    //tabname:name1 审核 name2
    getNextRole(param, tabname) {
      this.axios
        .post("/bpm/getNextRole", JSON.stringify(param), {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            if (tabname == "name1") {
              this.treeArr1 = response.data;
              this.treeArr1.forEach(item => {
                item.expand = true;
              });
              this.treeNLeafNodeDisable(this.treeArr1);
            } else {
              this.treeArr2 = response.data;
              this.treeArr2.forEach(item => {
                item.expand = true;
              });
              this.treeNLeafNodeDisable(this.treeArr2);
            }
          }
        })
        .catch(error => {});
    },
    //查询角色信息-转办
    getTransRoleUser() {
      this.axios
        .post("/system/transRoleUser?nodeId=" + this.nodeId, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.treeArr2 = response.data.roleUserList;
            this.treeArr2.forEach(item => {
              item.expand = true;
            });
            this.treeNLeafNodeDisable(this.treeArr2);
          }
        })
        .catch(error => {});
    },
    showDialog(tbodys) {
      console.log('tbodys', tbodys)
      if(tbodys.assistFlag){
        this.batchId = tbodys['param'];
        this.showModal = true;
        this.tabview = "assessDetail";
        this.dataObjj = {
        busId: tbodys['param'],
        newBusId: "",
        type: "",
        evalBusinessId: "",
        adjBusinessId: ""
      };
      }
    },
    backHome() {
      this.$router.go(-1);
    },
    fileDownload(index) {
      let multData = new FormData();
      let fileName = this.uploadList[index].name;
      multData.append("fileId", this.uploadList[index].url);
      this.axios
        .post(`/bpm/download`, multData, {
          headers: { "Content-Type": "multipart/form-data" },
          responseType: "blob"
        })
        .then(res => {
          this.download(res, fileName);
        })
        .catch(function(error) {});
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    // 产品风险审核提交
    taskBtnFun(name) {
      let that = this;
      let data = {
        documentId: JSON.parse(this.uploadParams.params).documentId,
        approveNote: this[name].desc, //审核意见
        approveResult: this[name].gender, //审核结果
        returnData: true,
        username: "",
        usernames: this[name].treeArr1,
        id: ""
      };
      data = JSON.stringify(data);
      this.$refs[name].validate(valid => {
        if (valid && this[name].gender == "0") {
          this.axios
            .post("/risk/task/doBusRiskAdjustTaskCommit", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response && response.code == "200") {
                this.$Message.info("提交成功");
                this.$router.push({
                  name: "businessAdjust",
                  query: {
                    
                  }
                });
                
              } else {
                this.$Message.info(response.message);
              }
            })
            .catch(error => {});
          // -----------
        } else if (valid && this[name].gender == "1") {
          let data2 = Object.assign({}, this.treeParam);
          if (this[name].desc.trim().length == 0) {
            this.$Message.info("请填写退回意见");
            return false;
          }
          (data2.approveNote = this[name].desc), //审核意见
            (data2.approveResult = this[name].gender), //审核结果
            this.axios
              .post("/risk/task/doBusRiskAdjustTaskCommit", JSON.stringify(data2), {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response && response.code == "200") {
                  this.$Message.info("提交成功");
                  this.$router.push({
                    path: "businessAdjust"
                  });
                } else {
                  this.$Message.info(response.message);
                }
              })
              .catch(error => {});
        }
      });
    },
    // 产品风险审核转办
    doTransSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          let data = {
            documentId: JSON.parse(this.uploadParams.params).documentId,
            approveNote: this[name].desc, //审核意见
            approveResult: "", //审核结果
            currentUser: this.treeParam.currentUser, //当前用户
            returnData: true,
            targetUser: this[name].treeArr1, //目标用户
            taskId: true //任务编号
          };
          this.axios
            .post("/extract/task/doTransSubmit", JSON.stringify(data), {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response && response.code == "200") {
                this.$Message.info("提交成功");
                this.$router.push({
                  path: "businessAdjust"
                });
              } else {
                this.$Message.info("提交失败");
              }
            })
            .catch(error => {
              this.$Message.info("提交失败");
            });
          // -----------
        }
      });
    },
    //禁用非叶子节点
    treeNLeafNodeDisable(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children) {
          //treeDatas[i].disabled = true;
          this.$set(treeDatas[i], "disabled", true);
          this.treeNLeafNodeDisable(treeDatas[i].children);
        }
      }
    },
    updateNextUserInfo(nodes, node) {
      nodes.forEach(el => {
        if (el.nodeKey != node.nodeKey) {
          el.checked = false;
        }
      });
      this.formValidate.treeArr1 = "";
      nodes.forEach(element => {
        if (element.checked == true) this.formValidate.treeArr1 = element.code;
      });
      this.$refs["formValidate"].validateField("treeArr1");
    },
    updateNextUserInfo2(nodes, node) {
      nodes.forEach(el => {
        if (el.nodeKey != node.nodeKey) {
          el.checked = false;
        }
      });
      this.formValidate2.treeArr1 = "";
      nodes.forEach(element => {
        if (element.checked == true) this.formValidate2.treeArr1 = element.code;
      });
      this.$refs["formValidate2"].validateField("treeArr1");
    },
    handleUploadfile(file) {
      let check = 0;
      this.uploadList.forEach(element => {
        if (
          parseInt(element.activityId) ==
          parseInt(JSON.parse(this.uploadParams.params).activityId)
        ) {
          check++;
        }
      });
      check = check < 5;
      if (!check) {
        this.$Notice.warning({
          title: "当前节点只能上传最多五个附件."
        });
      }
      return check;
    },
    handleSuccess(response, file) {
      if (response.code == "200") {
        file.url = response.data.dataVo.files[0].id;
        file.creatorName = response.data.dataVo.files[0].creatorName;
        file.activityId = response.data.dataVo.files[0].activityId;
      }
    },
    handleError() {
      this.$Message.error("上传失败");
    }
  }
};
</script>
